<template lang="en">
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="false" :showCheckBox="false" :listType="[DetailListType]" ModuleName="Approval" SubModuleCode="Approval" IdentityColumn="APPROVALCHAINID" :HeaderText="$t('ApprovalChain')" :ListData="AssetGroupingList" :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'RankUp'">
                <i class="fa fa-arrow-up text-primary action-icon down"></i>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'RankDown'">
                <i class="fa fa-arrow-down text-primary action-icon down"></i>
            </template>
            <!-- <template v-if="data.column.COLUMN_NAME == 'Action'">
                <div>
                <td class="text-center single-action">
                    <div class="cstm-drop-btn">
                        <span><a v-on:click="Delete(data.row.APPROVALCHAINID)"><em class="text-primary" aria-hidden="true"><i class="fa fa-trash text-danger action-icon">
                                    </i></em></a></span>
                    </div>
                </td>
            </template> -->
        </template>
        
    </tg-list>
    <div class="row flex-row-reverse mb-2">
        <div class="col-lg-12">
            <div class="py-2">
                <span class="text-danger"><small><span class="text-danger">Order number can be&nbsp;changed&nbsp;by clicking rank up/down arrow button, approval request priority from higher to lower will be followed by order number lower to higher.</span></small></span>
            </div>
        </div>
    </div>
</section>
</template>

<script>
import DataService from '../../service/DataService';
import Schema from '../ApprovalChain/GroupListSchema.js'
export default {

    data() {
        return {
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            DetailListType: 'List',
            listheaderbuttons: Schema.buttons,
            AssetGroupingList: [],
            IsShowGroup: false,
            noRecord: true,
            TotalRecords: 0,
            Headers: [{
                    "COLUMN_NAME": 'GROUPNAME',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ApprovalGroupName'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'USERS',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Users'),
                    "DISPLAY_ORDER": 2,
                },
                {
                    "COLUMN_NAME": 'RankUp',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('RankUp'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'RankDown',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('RankDown'),
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'APPROVERORDER',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Order',
                    "DISPLAY_ORDER": 5,
                },
                {
                    "COLUMN_NAME": 'Action',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Action'),
                    "DISPLAY_ORDER": 6,
                }
            ],

        }

    },
    created: function () {
        if (this.$route.params.id != null) {
            this.ApprovalChainId = this.$route.params.id;
        }
        this.FetchData();
    },

    methods: {
        async FetchData() {
            var vm = this;
            var url = `id=${this.$route.params.id}`
            await DataService.GroupList(url).then(response => {
                debugger
                if (response.data != null) {
                    if (response.data.length > 0) {
                        vm.AssetGroupingList = JSON.parse(response.data);
                        vm.noRecord = false;
                    } else {
                        vm.noRecord = true;
                    }
                }
            });
        },
        ClosePopup() {
            var vm = this;
            vm.$parent.CloseGroupPopup();
        },
        BackTolist() {
            this.$router.push({
                name: 'Approval Chain'
            });
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "DeleteRule":
                    this.DeleteRule()
                    break;
                case "BackTolist":
                    this.BackTolist()
                    break;
            }
        },

    },
}
</script>
